import React from "react";

function Feedback() {
  return (
    <section className="feedback-form">
      <div className="container">
        <div className="feedback-form__wrapper">
          <h2 className="feedback-form__title title">
              <span className="break-line">  !</span>
          </h2>
          <form className="feedback-form__form" action="" method="post">
            <div className="feedback-form__inputs-wrapper">
              <p className="feedback-form-input-wrapper main-input">
                <input className="feedback-form__input main-input__element" placeholder=" " type="text" name="name" id="feedback-name-field"/>
              </p>  
              <p className="feedback-form-input-wrapper main-input">
                <input className="feedback-form__input main-input__element" placeholder=" " type="email" name="email" id="feedback-email-field"/>
              </p>
            </div>
            <div className="feedback-form__button-wrapper">
              <button className="feedback-form__button button" type="submit"></button>
              <p className="feedback-form__consent">
                  ,       
              </p>
            </div>
          </form>
        </div>
      </div>
    </section>
  )
}

export default Feedback;